import { SfHeading, SfButton } from '@storefront-ui/vue';
import { useCheckout, checkoutGetters } from '@vue-storefront/about-you';

export default {
  name: 'OrderReview',
  components: {
    SfHeading,
    SfButton
  },

  setup() {
    const { personalDetails, shippingDetails, chosenShippingMethod, chosenPaymentMethod } = useCheckout();

    return {
      personalDetails,
      shippingDetails,
      chosenShippingMethod,
      chosenPaymentMethod,
      checkoutGetters
    };
  }
};
